<template>
  <div>
    <h1>登录</h1>
    <div class="form">
      <a-form-model
          ref="loginForm"
          :rules="loginForm.rules"
          :model="loginForm.form"
          :label-col="layout.labelCol"
          :wrapper-col="layout.wrapperCol">
        <!-- ref 被用来给元素或子组件注册引用信息。引用信息将会注册在父组件的 $refs 对象上。此时FormModal的注册信息
         为loginForm， $refs是所有注册过ref的集合-->
        <a-form-model-item label="电话号码" ref="telephone" prop="telephone">
          <a-input v-model="loginForm.form.telephone" />
        </a-form-model-item>
        <a-form-model-item label="密码" ref="password" prop="password">
          <a-input v-model="loginForm.form.password" type="password"/>
        </a-form-model-item>
        <a-form-model-item :wrapperCol="itemWrapper">
          <div class="btn_register">
            <a @click="register">立即注册</a>
          </div>
        </a-form-model-item>
        <a-form-model-item :wrapperCol="itemWrapper">
          <a-button type="primary" @click="login">登录</a-button>
        </a-form-model-item>
      </a-form-model>
    </div>
  </div>
</template>

<script>
import { validMobile } from "@/utils/validate";
// import { login } from '@/api/user'
// import md5 from 'md5'

export default {
  name: 'index',
  data () {
    const validateMobile = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('请输入电话号码'))
      }
      if (!validMobile(value)) {
        return callback(new Error('请输入正确格式电话号码'))
      }
      callback()
    }
    return {
      loginForm: {
        rules: {
          telephone: [{ required: true, trigger: 'blur' , validator: validateMobile}],
          password: [{ required: true, message: '请输入密码', trigger: 'blur' }]
        },
        form: {
          telephone: '',
          password: ''
        }
      },
      layout: {
        labelCol: { span: 8 },
        wrapperCol: { span: 8 },
      },
      itemWrapper: { span: 24 }
    }
  },
  methods: {
    register () {
      // this.$router.push({ path: '/register' })
      //命名路由带路由参数 需要使用name 进行路由跳转
      this.$router.push({ name: 'register', params: { data: 'hhh' } })
    // 命名路由带查询参数 显示拼接在url上，刷新不丢失
    //   this.$router.push({ name: 'register', query: { data: 'hhh' } })
    },
    login () {
      this.$refs.loginForm.validate(valid => {
        if (valid) {
          console.log('loginForm', this.loginForm.form)
          // 普通写法
          // login(this.loginForm.form.telephone, md5(this.loginForm.form.password)).then(res => {
          //   console.log('登录', res)
          // })
          // 全局状态管理
          this.$store.dispatch('login',this.loginForm.form).then(() => {
            console.log('登录成功')
            this.$store.dispatch('getInfo').then(() => {
              this.$router.push({
                name: 'home'
              })
            })
          })
        } else {
          alert('请正确填写相关信息')
        }
      })
    }
  }
}
</script>

<style scoped>
.btn_register {
  width: 35%;
  margin: 0 auto;
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-end;
}
</style>

